.main{
  background-color: $black;
  color: $white;
  padding: 1em 0 3em;
  &__top{
    padding: 3.75em 0;
    border-top: 1px solid $white;
    border-bottom: 1px solid $white;
  }
  .h1{
    max-width: 22em;
  }
  &__bottom{
    border-bottom: 1px solid $white;
    &-inner{
      display: grid;
      grid-template-columns: 50% 50%;
    }
    &-col{
      padding: 3em 3em 3em 0;
    }
    &-col2{
      padding: 3em 0 3em 3em;
      border-left: 1px solid $white;
      display: flex;
      flex-direction: column;
    }
  }
  .text{
    font-size: 1.25em;
  }
  &__img{
    height: 26.875em;
    position: relative;
    img{
      position: absolute;
      right: 0;
      top: 0;
      height: 100%;
      width: 48.75em;
      display: block;
      object-fit: cover;
      max-width: calc(50vw - 6em);
    }
  }
  &__nav{
    display: flex;
    margin-top: auto;
    padding-top: 3em;
  }
  &__btn{
    height: 3.75em;
    padding: 0 2em;
    span{
      font-size: 1.25em;
    }
  }
}



@media screen and (max-width: 992px){
  .main{
    padding-bottom: 7.5em;
    &__top{
      padding: 2em 0;
    }
    .h1{
      &__small-mob{
        font-size: .44em;
        display: block;
        margin-top: .25em;
      }
    }
    &__bottom{
      border-bottom: none;
      padding-top: 2.75em;
      &-inner{
        grid-template-columns: 100%;
      }
      &-col{
        padding: 0;
      }
      &-col2{
        padding: 2.5em 1.25em 0;
        border-left: none;
      }
    }
    &__img{
      height: auto;
      img{
        position: static;
        width: 100%;
        max-width: unset;
      }
    }
    &__nav{
      justify-content: center;
    }
  }
}


@media screen and (max-width: 576px){
  .main{
    .h1{
      &__small-mob{
        font-size: .6em;
      }
    }
    .text{
      font-size: 1em;
    }
    &__bottom{
      &-col2{
        padding: 1.5em .5em 0;
      }
    }
    &__btn{
      height: 2.8125em;
      width: 100%;
      padding: 0 1em;
      span{
        font-size: 1em;
      }
    }
  }
}
