.why{
  padding-top: 3.75em;
  &__top{
    margin-bottom: 2.5em;
  }
}

.why-card{
  padding: 1.75em 0;
  border-top: 1px solid $black;
  &__inner{
    display: grid;
    grid-template-columns: 6.25em calc(100% - 19em) 12.5em;
    gap: .25em;
    align-items: center;
  }
  &__col{
    align-self: flex-start;
    padding-top: 2.75em;
  }
  &__col2{
    max-width: 35em;
  }
  &__num{
    font-size: 3.125em;
    color: $primary;
    font-weight: 600;
  }
  &__desc{
    font-size: 1em;
    line-height: 150%;
    margin-top: .5em;
  }
  &__img{
    @include imgCover(100%);
  }
  &__slider{
    width: 46.6875em;
    position: relative;
    .swiper{
      &-container{
        overflow: hidden;
      }
      &-slide{
        width: calc(100% / 3);
      }
      &-nav{
        position: absolute;
        right: 100%;
        bottom: 0;
        margin-right: 3.125em;
        display: flex;
      }
      &-button{
        @include bgImgSize(100%);
        width: 3.125em;
        height: 3.125em;
        position: static;
        margin: 0;
        &-prev{
          background-image: url(../img/prev.png);
        }
        &-next{
          background-image: url(../img/next.png);
        }
      }
    }
  }
  &__item{
    &-img{
      @include imgCover(88%);
    }
  }
}

.why-card2{
  .why-card__col{
    padding-top: 2em;
  }
  .why-card__col3{
    grid-column: 1/-1;
    display: flex;
    justify-content: flex-end;
  }
}


@media screen and (max-width: 992px){
  .why{
    .h3{
      text-align: left;
    }
  }

  .why-card{
    padding: 3em 0;
    &__inner{
      grid-template-columns: 4.25em calc(100% - 11em) 6.25em;
    }
    &__col{
      padding-top: .5em;
    }
    &__col2{
      max-width: 25em;
    }
    &__num{
      font-size: 2.5em;
    }
    &__title{
      max-width: 12em;
    }
    &__desc{
      margin-top: .75em;
    }
    &__slider{
      width: 100%;
      .swiper{
        &-slide{
          width: 50%;
        }
        &-nav{
          position: static;
          margin: 0;
        }
        &-button{
          position: absolute;
          top: 50%;
          margin: 0;
          transform: translateY(-50%);
          &-prev{
            background-image: url(../img/prev2.png);
            left: 10px;
          }
          &-next{
            background-image: url(../img/next2.png);
            right: 10px;
          }
        }
      }
    }
  }

  .why-card2{
    .why-card__col{
      padding-top: .5em
    }
    .why-card__col2{
      max-width: 27em;
    }
    .why-card__col3{
      padding-top: 1em;
    }
    .why-card__title{
      max-width: 100%;
    }
  }
}


@media screen and (max-width: 576px){
  .why-card{
    &__inner{
      grid-template-columns: 100%;
      gap: 2em;
    }
    &__col{
      padding-top: 0;
    }
    &__col3{
      display: none;
    }
    &__num{
      font-size: 1.5em;
    }
    &__desc{
      font-size: .875em;
      margin-top: 2em;
    }
    &__slider{
      .swiper-slide{
        width: 100%;
      }
      .swiper-button{
        width: 1.875em;
        height: 1.875em;
      }
    }
  }

  .why-card2{
    .why-card__col{
      padding-top: 0;
    }
    .why-card__col3{
      padding-top: 0;
    }
  }
}
