.header{
  padding: 1.875em 0 3.75em;
  background-color: $black;
  &__inner{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__logo{
    display: block;
    flex-shrink: 0;
    width: 12.5em;
    img{
      width: 100%;
      display: block;
    }
    ._view2{
      display: none;
    }
  }
  &__btn{
    padding: 0 1.875em;
    height: 2.5em;
    margin-left: .625em;
    span{
      font-size: .875em;
    }
  }
}

.burger{
  display: none;
}

.menu{
  display: none;
}


@media screen and (max-width: 992px){
  .header{
    padding: 0;
    position: relative;
    z-index: 10;
    &__inner{
      height: 5.3125em;
    }
    &__logo{
      width: 9.5em;
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
    }
    &__action{
      display: none;
    }
    &._open{
      .menu{
        opacity: 1;
        pointer-events: unset;
        &__wrap{
          transform: translateX(0);
        }
      }
    }
  }

  .burger{
    position: fixed;
    top: 2em;
    right: 2em;
    display: block;
    width: 1.375em;
    height: .875em;

    &__inner{
      width: 100%;
      height: 100%;
      position: relative;
      z-index: 2;
      overflow: hidden;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      span{
        transition: all .6s;
        width: 100%;
        height: 2px;
        display: block;
        background-color: $white;
        &:nth-child(2){
          transform: translateX(-50%);
        }
      }
    }
    &:not(:hover){
      .burger__inner span:nth-child(2){
        transform: translateX(50%) !important;
      }
    }
    &:before{
      content: '';
      @include centerAbsolute;
      width: 3em;
      height: 3em;
      border-radius: 50%;
      background-color: $black;
    }
  }

  .menu{
    overflow: hidden;
    display: block;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: $vh;
    color: $white;
    transition: all .3s;
    opacity: 0;
    pointer-events: none;
    &__bg{
      position: fixed;
      top: 0;
      left: 0;
      width: 100%;
      height: $vh;
      opacity: .8;
      background-color: $black;
    }
    &__wrap{
      transform: translateX(100%);
      transition: all .3s;
      width: 18.75em;
      position: fixed;
      top: 0;
      right: 0;
      height: $vh;
      background-color: $black;
      display: flex;
      flex-direction: column;
      overflow-y: auto;
      padding: 2.625em 1.5em 1.5em;
    }
    &__close{
      position: absolute;
      top: .25em;
      right: .25em;
      width: 2.5em;
      height: 2.5em;
      @include center;
      img{
        width: 1.25em;
      }
    }
    &__links{
      @include grid100gap(1em);
      a{
        font-size: 1em;
        line-height: 140%;
      }
    }
    &__bottom{
      padding-top: 3em;
      margin-top: auto;
    }
    &__text{
      font-size: .8125em;
      line-height: 145%;
    }
    .socials{
      margin-top: 1.5em;
    }
    &__nav{
      display: flex;
      margin-top: 1.5em;
    }
    &__btn{
      padding: 0 1.5em;
      span{
        font-size: .875em;
      }
    }
  }
}


