.delivery{
  border-top: 1px solid $black;
  &__grid{
    display: grid;
    grid-template-columns: 50% 50%;
  }
  &__col{
    padding: 3em 2em 3em 0;
    display: flex;
    align-items: center;
  }
  &__col2{
    padding: 3em 0 3em 2em;
    border-left: 1px solid $black;
  }
  &__img{
    width: 100%;
    display: block;
    margin-bottom: 1em;
  }
}

.improve{
  padding: 3.75em 0 6em;
  background-color: $black;
  color: $white;
  &__bottom{
    margin-top: 3em;
    padding-top: 3em;
    border-top: 1px solid $white;
  }
  &__desc,
  &__desc2{
    @include grid100gap(.5em);
    p{
      line-height: inherit;
    }
  }
  &__desc{
    a{
      font-weight: 700;
    }
  }
  &__desc2{
    width: 27em;
    max-width: 100%;
    margin-top: 1.5em;
    margin-left: auto;
  }
  &__img{
    width: 100%;
    display: block;
    margin-top: -10em;
  }
}

.feedback{
  padding: 4em 0 5em;
  &__top{
    @include grid100gap(.5em);
  }
}

.feedback-form{
  margin: 4em auto 0;
  width: 51em;
  max-width: 100%;
  &__fields{
    @include grid100gap(1.25em);
  }
  &__nav{
    margin-top: 1.25em;
    display: flex;
    justify-content: center;
  }
  &__hint{
    margin-top: 1em;
    text-align: center;
    a{
      text-decoration: underline;
    }
  }
}


@media screen and (max-width: 992px){
  .delivery{
    &__col{
      padding: 2em .5em 2em 0;
    }
    &__col2{
      padding: 2em 0 2em .5em;
    }
    &__img{
      margin-bottom: .75em;
    }
  }

  .improve{
    padding: 3em 0 5em;
    .h2{
      text-align: left;
    }
    &__bottom{
      margin-top: 2em;
      padding-top: 2em;
    }
    &__desc2{
      display: none;
    }
    &__img{
      margin-top: 2em;
    }
  }

  .feedback{
    padding: 3em 0;
  }

  .feedback-form{
    margin-top: 1.5em;
  }
}


@media screen and (max-width: 576px){
  .delivery{
    text-align: center;
    padding: 2em 0;
    &__grid{
      grid-template-columns: 100%;
    }
    &__col{
      padding: 0;
    }
    &__col2{
      border: none;
      padding: 1em 0 0;
    }
  }

  .feedback{
    &__top{
      text-align: left;
    }
  }
}
