.faq{
  padding: 3.75em 0 6.5em;
  background-color: $black;
  color: $white;
  &__top{
    padding: 3.75em 0 1.875em;
    border-top: 1px solid $white;
  }
  .h2{
    padding-left: 1em;
  }
}

.qa-card{
  border-top: 1px solid $white;
  &:last-child{
    border-bottom: 1px solid $white;
  }
  &._active{
    .qa-card__btn svg{
      transform: rotate(-45deg);
    }
  }
  &__show{
    padding: 2.75em 0;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: space-between;
    min-height: 5.25em;
  }
  &__btn{
    margin-left: 1em;
    width: 2.5em;
    height: 2.5em;
    background-color: $white;
    border-radius: 50%;
    padding: .5em;
    @include center;
    flex-shrink: 0;
    svg{
      transition: all .3s;
      width: 100%;
    }
    g{
      stroke: $black;
    }
    &:hover{
      g{
        stroke: $primary;
      }
    }
  }
  &__hidden{
    padding-bottom: 2.75em;
    display: none;
  }
  .text{
    max-width: 43.75em;
    font-weight: 500;
  }
}

.text{
  font-size: 1em;
  line-height: 150%;
  *{
    line-height: inherit;
  }
  p,ul{
    @include lastM(0 0 1em);
  }
  ul{
    li{
      position: relative;
      padding-left: 1.25em;
      &:before{
        content: 'â€¢';
        font-size: 2em;
        position: absolute;
        top: -.125em;
        left: 0;
      }
    }
  }
}




@media screen and (max-width: 992px){

}


@media screen and (max-width: 576px){
  .faq{
    padding: 1.875em 0;
    &__top{
      padding-top: 1.875em;
    }
  }

  .qa-card{
    &__show{
      padding: 1em 0;
    }
    &__hidden{
      padding-bottom: 1.5em;
    }
    &__title{
      font-size: 1em;
    }
    &__btn{
      background-color: transparent;
      g{
        stroke: $primary;
      }
    }
  }

  .text{
    font-size: .875em;
  }
}
