.footer{
  padding: 2.5em 0 6.25em;
  background-color: $black;
  color: $white;
  .container{
    max-width: 100%;
  }
  &__text{
    font-size: .875em;
    line-height: 150%;
    font-weight: 100;
  }
}

.scroll-up{
  display: inline-flex;
  align-items: center;
  transition: all .3s;
  cursor: pointer;
  &:hover{
    color: $primary;
    path{
      fill: $primary;
    }
  }
  svg{
    width: .3125em;
    margin-left: .25em;
  }
}


@media screen and (max-width: 992px){

}


@media screen and (max-width: 576px){
  .footer{
    text-align: center;
    &__inner{
      @include grid100gap(1.5em);
    }
    .socials{
      order: -1;
      justify-content: center;
    }
    .scroll-up{
      margin: 0 auto;
    }
  }
}
